<template>
  <div>
    <div class="login-body">
      <div class="app-logo">
        <img class="logo" src="@/assets/img/logo.png" />
        <span class="title">边互通后台管理系统</span>
      </div>
      <img class="gy" src="@/assets/img/gy.png" />
    </div>
    <div class="login-footer">
      <div class="login-form">
        <Form
          class="form"
          ref="formInline"
          :model="formInline"
          :rules="ruleInline"
        >
          <FormItem prop="phone">
            <Input
              id="logininput"
              type="text"
              class="login-input"
              v-model="formInline.phone"
              placeholder="请输入账号"
              style="width: 250px"
            >
            </Input>
          </FormItem>
          <FormItem prop="password">
            <Input
              id="logininput"
              type="password"
              class="login-input"
              v-model="formInline.password"
              placeholder="请输入密码"
              style="width: 250px"
            >
            </Input>
          </FormItem>
          <FormItem style="margin-bottom:0px">
            <Button class="btn" @click="handlerLogin('formInline')"
              >立即登录</Button
            >
          </FormItem>
        </Form>
        <span class="support">云南大通汇国际电子商务有限公司技术支持</span>
      </div>
    </div>
  </div>
</template>
<script>
/**
 * 登录页面
 */
import { login } from '@/api/admin.js'
import Cookies from 'js-cookie'
import { SERVER_CODE } from '@/utils/constants.js'

export default {
  data() {
    return {
      loading: false,
      formInline: {
        phone: '',
        password: ''
      },
      ruleInline: {
        phone: [
          {
            required: true,
            message: '请输入用户名',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '请输入密码',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    handlerLogin(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.loading = true
          login(this.formInline)
            .then(({ code, data, msg }) => {
              if (code === SERVER_CODE.CODE_200) {
                Cookies.set('TOKEN', data.token, { expires: 7 })
                this.$Message.success('登录成功！')
                this.$router.push('/user')
              } else {
                this.$Message.info(msg)
                console.log('登录失败')
              }
            })
            .catch(error => {
              console.log(error)
            })
            .finally(() => {
              this.loading = false
            })
        }
      })
    }
  }
}
</script>
<style scoped lang="scss">
.login-body {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70vh;
  background-color: #007bff;
  .app-logo {
    z-index: 10;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .logo {
      width: 182px;
      height: 54px;
    }
    .title {
      margin-top: 12px;
      font-size: 26px;
      color: #f8f9fa;
    }
  }
  .gy {
    position: absolute;
    bottom: 0;
    width: 100%;
  }
}
.login-footer {
  height: 30vh;
  background-color: #155395;
  .login-form {
    display: flex;
    margin-left: 10%;
    padding-top: 40px;
    .form {
      padding-right: 30px;
      border-right: 1px solid #61d9ff;
      .btn {
        background-color: #2d8cf0;
        border-color: #2d8cf0;
        color: #fff;
        cursor: pointer;

        &:hover {
          background-color: #57a3f3;
          border-color: #57a3f3;
        }
      }
    }
  }

  .support {
    margin-left: 30px;
    font-size: 14px;
    color: #61d9ff;
  }
}
</style>
