/**
 * 系统管理接口
 */
import http from '@/utils/http.js'
import { API_URL } from '@/utils/constants.js'

/**
 * 管理员登录系统
 * @param {*} params 登录参数
 */
export const login = params => {
  return http.post(API_URL.LOGIN_URL, params, {
    headers: { 'Content-Type': 'application/json' }
  })
}
